var React=require("react");
var filelist=require("./filelist.js");
var Volmenu = React.createClass({displayName: "Volmenu",
  getInitialState:function() {
    return {};
  },
  doGetBampoByVol: function(e){
  	this.props.getBampoByVol(e);
  },
  renderVol: function(key){
  	var num = key.replace(/[a-z]/g,"");
  	if(key === "vol081"){
	  	return(
			React.createElement("option", {value: key, selected: true}, num)
	  	)  		
	} else {
	  	return(
			React.createElement("option", {value: key}, num)
	  	)  
	} 

  },
  render: function() {
  	var vols = Object.keys(filelist).map(this.renderVol);

    return React.createElement("div", null, 
    	React.createElement("select", {onChange: this.doGetBampoByVol}, 
    		vols
    	)
    );
  }
});
module.exports=Volmenu;