var React=require("react");
var Volmenu=require("./volmenu.jsx");
var Bampomenu=require("./bampomenu.jsx");

var Menu = React.createClass({displayName: "Menu",
  getInitialState:function() {
    return {vol:"vol081"};
  },
  getBampoByVol: function(e){
    this.setState({vol:e.target.value});
  },
  getTextByBampo: function(e){
    this.props.getText(e.target.value);
  },
  render: function() {
    return React.createElement("div", null, 
    	React.createElement(Volmenu, {getBampoByVol: this.getBampoByVol}), 
      React.createElement(Bampomenu, {vol: this.state.vol, getTextByBampo: this.getTextByBampo})
    );
  }
});
module.exports=Menu;